vg\"\n                                alt=\"\"\n                                style=\"margin-right: 5px\"\n                            />\n                            Recent orders\n                        </div>\n                        <div class=\"orders-content\">\n                            <div class=\"order-list\">\n                                <div\n                                    class=\"d-flex justify-content-between py-2 px-1\"\n                                    :style=\"item.style\"\n                                    v-for=\"(item, index) in orders\"\n                                    :key=\"index\"\n                                >\n                                    <span>{{ item.status }}</span>\n                                    <span>{{ item.value }}</span>\n                                </div>\n                            </div>\n                            <div class=\"order-percent\">\n                                <!-- <CircleProgress\n                                :percent=\"60\"\n                                :fill-color=\"`#A6D997`\"\n                                :empty-color=\"`#DFEEDB`\"\n                                :size=\"160\"\n                                :viewport=\"true\"\n                                :show-percent=\"true\"\n                                style=\"align-self: center\"\n                            /> -->\n                            </div>\n                        </div>\n                    </div>\n                    <div class=\"bride-calander-cont\">\n                        <Calendar\n                            :attributes=\"attr\"\n                            :masks=\"{ title: 'MMMM' }\"\n                            @dayclick=\"dayclicked\"\n                        >\n                            <template #day-popover=\"{ dayTitle }\">\n                                <div\n                                    class=\"text-xs text-gray-300 font-semibold text-center\"\n                                >\n                                    {{ dayTitle }}\n                                </div>\n                                <div\n                                    v-for=\"(item, index) in todos\"\n                                    :key=\"index\"\n                                >\n                                    <div\n                                        v-if=\"\n                                            `'` + item.dates + `'` ===\n                                            `'` +\n                                                new Date(\n                                                    date.year,\n                                                    date.month,\n                                                    date.day\n                                                ) +\n                                                `'`\n                                        \"\n                                    >\n                                        <div\n                                            v-for=\"(\n                                                item, index\n                                            ) in item.description\"\n                                            :key=\"index\"\n                                        >\n                                            {{ item }}\n                                        </div>\n                                    </div>\n                                </div>\n                            </template>\n                        </Calendar>\n                        <div class=\"calander-add-schedule\">\n                            <div\n                                v-if=\"date.name\"\n                                class=\"calander-add-schedule-date\"\n                            >\n                                {{ date.name }}\n                            </div>\n                            <div v-else class=\"calander-add-schedule-date\">\n                                {{ current }}\n                            </div>\n                            <div class=\"calander-add-schedule-form\">\n                                <button @click=\"showForm\">Add Schedule</button>\n                                <form @submit.prevent=\"addTodo\" action=\"\">\n                                    <input\n                                        type=\"text\"\n                                        name=\"\"\n                                        id=\"\"\n                                        v-model=\"todoValue\"\n                                    />\n                                    <button type=\"submit\">Submit</button>\n                                </form>\n                                <div>\n                                    <div\n                                        v-for=\"(item, index) in todos\"\n                                        :key=\"index\"\n                                    >\n                                        <div\n                                            v-if=\"\n                                                `'` + item.dates + `'` ===\n                                                `'` +\n                                                    new Date(\n                                                        date.year,\n                                                        date.month,\n                                                        date.day\n                                                    ) +\n                                                    `'`\n                                            \"\n                                        >\n                                            <div\n                                                v-for=\"(\n                                                    item, index\n                                                ) in item.description\"\n                                                :key=\"index\"\n                                                style=\"word-break: break-word\"\n                                            >\n                                                {{ item }}\n                                            </div>\n                                        </div>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"leads-and-schedule-cont\">\n                    <div\n                        class=\"splide\"\n                        id=\"order-splide\"\n                        style=\"padding-bottom: 10px\"\n                    >\n                        <div class=\"splide__track\">\n                            <ul class=\"splide__list\">\n                                <li\n                                    class=\"splide__slide\"\n                                    style=\"\n                                        border: none;\n                                        align-self: end;\n                                        cursor: default;\n                                    \"\n                                    v-for=\"(item, index) in upcomingOrders\"\n                                    :key=\"index\"\n                                >\n                                    <div\n                                        class=\"new-order\"\n                                        v-if=\"item.seen == 0\"\n                                    >\n                                        New Leads\n                                    </div>\n                                    <div class=\"new-leads\">\n                                        <div class=\"new-lead-content\">\n                                            <div>\n                                                <img\n                                                    src=\"\\assets\\vendors\\dashboard\\Ellipse-81.png\"\n                                                    alt=\"\"\n                                                />\n                                                <div>\n                                                    <div class=\"new-lead-name\">\n                                                        {{ item.name }}\n                                                    </div>\n                                                    <div class=\"new-lead-info\">\n                                                        {{ item.location }}\n                                                    </div>\n                                                </div>\n                                            </div>\n\n                                            <div>\n                                                <div class=\"new-lead-info\">\n                                                    {{ item.date }}\n                                                </div>\n                                            </div>\n                                        </div>\n                                        <div class=\"new-leads-single-details\">\n                                            <div>\n                                                <span>{{ item.date }}</span>\n                                                <i\n                                                    class=\"far fa-calendar-check\"\n                                                ></i>\n                                            </div>\n                                            <div>\n                                                <span>{{ item.time }}</span>\n                                                <i class=\"far fa-clock\"></i>\n                                            </div>\n                                            <div>\n                                                <span>{{ item.loc }}</span>\n                                                <i\n                        